* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container-portfolio {
  padding: 20px 0;
  background-color: teal;
  background-image: linear-gradient(to left, #00baba, #008b8b);
  display: flex;
}

.container-fluid {
  width: 500px;
  display: flex;
  flex-wrap: wrap;
  margin-left: 45px;
}

.container-box {
  background-color: white;
  width: 1px solid black;
  width: 250px;
  height: 250px;
  padding: 20px;
  margin: 10px;
}

.container-box img {
  width: 250px;
  height: 250px;
  margin-left: -40px;
  margin-top: -20px;
}

.overlay {
  position: absolute;
  margin-bottom: 200px;
  margin-top: -254px;
  margin-left: -20px;
  height: 250px;
  width: 250px;
  opacity: 0;
  transition: 0.5s ease;
  background-color: black;
}

.text {
  color: white;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

.container-box:hover .overlay {
  opacity: 1;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 320px) {
  .container-fluid {
    margin-left: 25px;
  }
}

@media only screen and (min-width: 425px) {
  .container-fluid {
    margin-left: 60px;
  }
}
@media only screen and (min-width: 768px) {
  .container-fluid {
    margin-left: 100px;
  }
}

@media only screen and (min-width: 992px) {
  .container-fluid {
    margin-left: 200px;
  }
}
